.userList{
    flex: 4;
}
.userListUser{
    display: flex;
    align-items: center;

}

.userListImg{
    width: 32px;
    height: 32px;
    border-bottom: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.userListEdit{
    border: none;
    border-radius: 10px;
    margin: 4px 5px;
    background-color:#3bb077;
    color: white;
    cursor: pointer;
    margin-right: 10px;
}
.userListDelete{
color: red;
cursor: pointer;
}