.widgetlg{
    flex: 2;
    -webkit-box-shadow: 0px 0px 11px -1px #000000; 
box-shadow: 0px 0px 11px -1px #000000;
padding: 20px;
}

.widgetLgTitle{
    font-size: 22px;
    font-weight: 600;
}

.widgetLgImg{
    width: 40px;
    height: 40px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 10px;
}

.widgetLgButton{
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
}
.widgetLgButton.Approved{
background-color: #e5faf2;
color: #3bb077;
}
.widgetLgButton.Declined{
background-color: #fff0f1;
color: #d95087;

}
.widgetLgButton.Pending{
background-color: #ebf1fe;
color: #2a7ade;

}

.widgetLgTable{
    width: 100%;
    border-spacing: 20px;
}

.widgetLgTr{
    text-align: left;
}

.widgetLgUser{
    display: flex;
    align-items: center;
}

.widgetLgDate, .widgetLgAmount,
.widgetLgStatus{
font-weight: 300;
}