.featured{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.featuredItem{
    flex: 1;
    margin: 0 20px;
    padding: 30px;
    border-radius: 10px;
    -webkit-box-shadow: 0px 0px 11px -1px #000000; 
        box-shadow: 0px 0px 11px -1px #000000;
}

.featuredTitle{
    font-size: 20px;
}

.featureMoneyContainer{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}

.featuredMoney{
    font-size: 30px;
    font-weight: bold;
}

.featuredRate{
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.featureIcon{
    font-size: 14px;
    margin-left: 5px;
    color: green;
}
.featureIcon.negative{
    color: red;
}

.featuredSub{
    font-size: 15px;
    color: green;
}